import React, { Fragment, useEffect, useState } from "react";
import {
    Dialog, DialogTitle, DialogContent,
    DialogContentText, DialogActions,
    Button, withMobileDialog
} from "@material-ui/core";
import { handleDelete } from "./ajax";

function DeleteNarrativeModal (props) {
    const {
        fullScreen, isOpen,
        closeHandler, chosenNarrative, 
        setIsUpdateNeeded, deleteMultipleIDs, deleteMultipleNarrative
    } = props;
    const { id: narrative_id, narrative } = chosenNarrative;

    useEffect(() => {
    }, [deleteMultipleIDs, deleteMultipleNarrative, narrative_id])

    const handleDeleteClick = () => {
        closeHandler();
        const payload = {
            narrative_id
        };
        console.log("Single:", payload)
        handleDelete(payload, ret => {
            setIsUpdateNeeded(true);
        });
    };

    const handleMultipleDelete = () => {
        deleteMultipleIDs.forEach(narrative_id => {
            const payload = {
                narrative_id
            };
            console.log("Multiple:", payload)
            handleDelete(payload, ret => {
                setIsUpdateNeeded(true);
            })
        }); 
        closeHandler();
    }

    return (
        <div>
            <Dialog
                fullWidth
                fullScreen={fullScreen}
                open={isOpen}
                aria-labelledby="form-dialog-title"

            >
                <DialogTitle id="form-dialog-title">Site Logs Delete</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Are you sure you want to delete this narrative/s?
                    </DialogContentText>
                    <DialogContentText align="center">
                        { deleteMultipleNarrative.length > 0 ? (
                            deleteMultipleNarrative.map((row, index) => {
                                return (
                                    <Fragment>
                                        {row}<br/>
                                    </Fragment>
                                )}
                                )) : (
                                    narrative
                                )}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <div>
                        <Button onClick={closeHandler} color="primary">
                            Cancel
                        </Button>
                        <Button variant="contained" color="secondary" disabled={false}
                            onClick={
                                deleteMultipleIDs.length > 1 ? (
                                    handleMultipleDelete
                                ) : (
                                    handleDeleteClick
                                )
                            }>
                            Confirm
                        </Button>
                    </div>
                </DialogActions>
            </Dialog>
        </div>
    );
}

export default withMobileDialog()(DeleteNarrativeModal);
